.btn {
  display: inline-flex;
  padding: 10px 20px;

  font-family: $ff-roboto-condenced;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;

  letter-spacing: 0.05em;

  color: $color-default-white;
  background-color: $color-black-pearl;
  border: none;
  border-radius: 3px;
  cursor: pointer;

  transition: opacity $trans-default;

  text-transform: uppercase;

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }
}


.btn-call-request {
  padding: 11px 15px;
  background-color: $color-transparent;
  border: 1px solid $color-alice-blue;
  margin-right: 60px;

  @include vp-1023 {
    display: none;
  }
}

.btn-accordion {
  margin: 5px 0 20px;
  padding: 0;
  background-color: $color-transparent;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;

  @include vp-1023 {
    margin-top: 0;
  }

  @include vp-767 {
    margin: 0;
    width: 100%;
    padding: 20px 25px;
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 15px;
      height: 1px;
      background-color: $color-silver;
      right: 25px;
      top: 27px;
    }

    &::after {
      transform: rotate(90deg);
    }
  }
}

.btn-consultation {
  color: $color-alice-blue;
  font-size: 18px;
  line-height: 21px;
  padding: 15px 25px;
  background-color: $color-deep-sky-blue;

  @include vp-1023 {
    margin-left: 45px;
  }

  @include vp-767 {
    margin: 0;
    padding: 10px 30px;
    font-size: 14px;
    line-height: 16px;
    width: 100%;
    justify-content: center;
  }
}

.btn-details {
  color: $color-deep-sky-blue;
  padding: 14px 50px;
  background-color: rgba($color-default-white, 0.7);
  border: 1px solid $color-deep-sky-blue;
  width: fit-content;
}

.btn-question {
  background-color: $color-black-pearl;
  padding: 15px 25px;
  font-size: 16px;
  line-height: 19px;
  width: 265px;
  display: block;

  @include vp-767 {
    width: 100%;
  }
}

.btn-callback {
  display: block;
  font-size: 16px;
  line-height: 18px;
  padding: 14px 60px;
  margin-top: 5px;
}

.btn-reset {
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 1px;
    height: 28px;
    top: 6px;
    left: 20px;
    background-color: $color-default-white;
    transform: rotate(-45deg);
  }

  &::after {
    transform: rotate(45deg);
  }
}
