.consultation {
  background-color: $color-deep-sky-blue;
  position: relative;
  scroll-margin-top: 120px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    clip-path: polygon(0 0, 0% 100%, 50% 0);
    background-color: rgba($color-black-pearl, 0.5);
  }

  @include vp-1023 {
    &::before {
      display: none;
    }
  }
}

.consultation__wrapper {
  position: relative;
  max-width: 1220px;
  padding: 170px 0 250px;
  margin: 0 auto;
  z-index: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @include vp-1023 {
    padding: 50px 0 125px;
  }

  @include vp-767 {
    padding: 50px 0 90px;
  }
}

.consultation__wrapper img {
  display: block;

  @include vp-1023 {
    display: none;
  }
}

.consultation__inner {
  width: 585px;
  margin-left: 30px;

  @include vp-1023 {
    width: 100%;
    margin-left: 0;
  }
}

.consultation__inner h2 {
  font-family: $ff-roboto-condenced;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
  margin: 0;
  margin-bottom: 15px;

  @include vp-767 {
    font-size: 30px;
    line-height: 42px;
    margin-bottom: 20px;
  }
}

.consultation__inner p {
  line-height: 22px;
  color: $color-default-white;
  margin: 0;
  margin-bottom: 35px;

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 40px;
  }
}
